import React from 'react';
import {
    Button,
    message,
} from 'antd';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import {
    CopyFilled
} from '@ant-design/icons';
import '../App.less';

const CopyClipboardButton = ({ text }) => {
    return (
        <CopyToClipboard onCopy={() => message.success('Copied!')} text={text}>
            <Button icon={<CopyFilled />} htmlType="submit" type="primary" shape="round" size={'medium'}>
                Copy to clipboard
            </Button>
        </CopyToClipboard>
    )
}

export default CopyClipboardButton;