import React, { useState } from 'react';
import {
    Layout, Row, Col, Typography,
} from 'antd';
import AvatarsForm from '../Form/AvatarsForm'
import '../../App.less';
import GeneratedCssContent from './GeneratedCssContent';
import { generateTemplateCss } from '../../helper/CssGeneratorHelper'

const { Title, } = Typography;
const { Content } = Layout;

const GenerateContent = ({ isVisible }) => {
    const [generatedCss, setGeneratedCss] = useState(generateTemplateCss('', 50, 5));

    const handleSubmit = (event) => {
        setGeneratedCss(event.finalCss)
    }

    if (!isVisible) return null;
    return (
        <Content>
            <Row>
                <Col span={10}>
                    <Title level={4}>Generate Custom Discord Avatar OBS Overlay</Title>
                    <AvatarsForm onSubmit={handleSubmit} />
                </Col>
                <Col span={1} />
                <Col span={11}>
                    <GeneratedCssContent generatedCss={generatedCss} />
                </Col>
            </Row>
        </Content>
    )
}

export default GenerateContent;