import React from 'react';
import {
    Layout, Typography,
} from 'antd';
import '../../App.less';
import CopyClipboardButton from '../CopyClipboardButton';
import { generateTemplateCss } from '../../helper/CssGeneratorHelper';

const { Title, Paragraph } = Typography;
const { Content } = Layout;

const GeneratedCssContent = ({ generatedCss }) => {
    const css = (generatedCss||generateTemplateCss('', 50, 5)).trim();

    return (
        <Content>
            <Title level={4}>Generated CSS</Title>
            <CopyClipboardButton text={css} />
            <Paragraph>
                <pre>{css}</pre>
            </Paragraph>
            <CopyClipboardButton text={css} />
        </Content>
    )
}

export default GeneratedCssContent;