import React from 'react';
import {
    Layout, Row, Col, Typography,
} from 'antd';
import '../../App.less';

const { Title, } = Typography;
const { Content } = Layout;

const DemoContent = ({ isVisible }) => {
    if (!isVisible) return null;
    return (
        <Content>
            <Row align={'middle'} justify={'center'}><Title align={'center'} level={1}>Set your own avatar for Discord voice overlay in OBS</Title></Row>
            <Row align={'middle'} justify={'center'}>
                <Col span={16}>
                    <video width="100%" height="auto" autoPlay controls muted loop>
                        <source src="./demo.mp4" type="video/mp4" />
                        Your browser does not support the video tag.
                    </video>
                </Col>
            </Row>
        </Content>
    )
}

export default DemoContent;