import React, { Fragment, useState } from 'react';
import {
  Form, Button, Typography,
  Input, Slider, Divider,
  Tooltip, message, Space
} from 'antd';
import {
  UserAddOutlined,
  ToolFilled,
  CloseCircleFilled
} from '@ant-design/icons';
import '../../App.less';
import { generateAvatarTemplate, generateTemplateCss } from '../../helper/CssGeneratorHelper'

const { Text, Paragraph } = Typography;


const AvatarsForm = ({ onSubmit }) => {
  const [accounts, setAccounts] = useState([]);
  const [nextAccount, setNextAccount] = useState(0);
  const [form] = Form.useForm();

  const jumpText = (<Fragment>
    <Paragraph>How high avatars jump up when they are speaking.</Paragraph>
    <Paragraph>
      <ul>
        <li>0% - No movement</li>
        <li>5% - Jump 5% height of avatar</li>
        <li>10% - Jump 10% height of the avatar</li>
      </ul>
    </Paragraph>
  </Fragment>);

  const brightnessText = <Text>How bright the avatars are when they are not speaking.</Text>;
  const userAccInputText = <Text>Right click the user on Discord then 'Copy ID'.</Text>;
  const avatarInputText = <Text>Link to an image. Works for JPEG, GIF, PNG. Other images may not work.</Text>;
  const speakingAvatarInputText = (<Fragment><Paragraph>If you want a different image when the user is speaking.</Paragraph>
    <Paragraph>Link to an image. Works for JPEG, GIF, PNG. Other images may not work.</Paragraph></Fragment>);

  const handleSubmit = (values) => {
    const userConfigs = accounts.map(account => {
      const userId = values[`userId-${account.num}`] || '';
      const avatarUrl = values[`avatarUrl-${account.num}`] || '';
      const speakingAvatarUrl = values[`speakingAvatarUrl-${account.num}`] || '';

      return {
        userId: userId.trim(),
        avatarUrl: avatarUrl.trim(),
        speakingAvatarUrl: speakingAvatarUrl.trim()
      }
    }).filter(config => !!config.userId);

    const avatarCss = generateAvatarTemplate(userConfigs)
    onSubmit({ finalCss: generateTemplateCss(avatarCss, values.brightness || 50, values.jumpHeight || 5) });
    message.success('Generated new CSS!')
  }

  const handleAddAvatar = () => {
    const num = nextAccount;
    setAccounts([...accounts, { num }])
    setNextAccount(nextAccount + 1);
  }

  const handleRemoveAvatar = (num) => {
    setAccounts(accounts.filter(account => account.num !== num))
  };

  return (
    <Form form={form} onFinish={handleSubmit} >
      <Text>Choose your avatar settings.</Text>
      <Tooltip placement="top" title={brightnessText}>
        <Form.Item name='brightness' initialValue={50} label="Inactive brightness" >
          <Slider min={0} max={100} tipFormatter={(value) => `${value}%`} />
        </Form.Item>
      </Tooltip>
      <Tooltip placement="top" title={jumpText}>
        <Form.Item name='jumpHeight' initialValue={5} label="Highlight avatar jump" >
          <Slider min={0} max={10} tipFormatter={(value) => `${value}%`} />
        </Form.Item>
      </Tooltip>
      {accounts.map((account) => (
        <Fragment key={`account-${account.num}`}>
          <Divider />
          <Tooltip placement="top" title={userAccInputText}>
            <Form.Item name={`userId-${account.num}`} label="Discord User ID" >
              <Input />
            </Form.Item>
          </Tooltip>
          <Tooltip placement="top" title={avatarInputText}>
            <Form.Item name={`avatarUrl-${account.num}`} label="Avatar Image Link" >
              <Input />
            </Form.Item>
          </Tooltip>
          <Tooltip placement="top" title={speakingAvatarInputText}>
            <Form.Item name={`speakingAvatarUrl-${account.num}`} label="Highlight Avatar Image ID">
              <Input />
            </Form.Item>
          </Tooltip>
          <Button size={'small'} icon={<CloseCircleFilled />} onClick={() => handleRemoveAvatar(account.num)} danger>Remove account</Button>
        </Fragment>)
      )}
      <Divider />
      <Space direction='vertical' size={'middle'}>
        <Button shape="round" icon={<UserAddOutlined />} size={'small'}
          onClick={handleAddAvatar}>
          Add account specific avatar
        </Button>
        <Paragraph>
          <Button icon={<ToolFilled />} htmlType="submit" type="primary" shape="round" size={'medium'}>
            Generate Custom CSS
          </Button>
        </Paragraph>
      </Space>
    </Form>
  )
}

export default AvatarsForm;