import React, { useState } from 'react';
import {
    Layout, Row, Col, Typography, Image,
} from 'antd';
import AvatarsForm from '../Form/AvatarsForm'
import '../../App.less';
import GeneratedCssContent from './GeneratedCssContent';

const { Title, Text, Link, Paragraph } = Typography;
const { Content } = Layout;

const FirstTimeContent = ({ isVisible }) => {
    const [generatedCss, setGeneratedCss] = useState('');

    const handleSubmit = (event) => {
        setGeneratedCss(event.finalCss)
    }
    if (!isVisible) return null;
    return (
        <Content>
            <Row>
                <Col span={10}>
                    <Title level={4}>First Time Guide</Title>
                    <Image.PreviewGroup>
                        <ol>
                            <li>
                                <Text>Generate a Voice Widget link from <Link href='https://streamkit.discord.com/overlay' target='_blank'>Discord StreamKit</Link> and choose 'Install for OBS'.</Text>
                                <Paragraph><Image style={{maxHeight:'150px'}} src="./step1.png" /></Paragraph>
                                <Paragraph>You will need to authorize access to your Discord account. Check your Discord app now!</Paragraph>
                                <Paragraph><Image style={{maxHeight:'150px'}} src="./step2.png" /></Paragraph>
                            </li>
                            <li>
                                <Text>Choose 'Voice Widget'</Text>
                                <Paragraph><Image style={{maxHeight:'150px'}} src="./step3.png" /></Paragraph>
                            </li>
                            <li>
                                <Text>Set the server and channel you want the overlay to display.</Text>
                                <Paragraph><Text type="warning" strong>Disable 'Show Speaking Users Only' or you will get unintended cuts.</Text></Paragraph>
                                <Paragraph><Image style={{maxHeight:'150px'}} src="./step4.png" /></Paragraph>
                            </li>
                            <li>
                                <Text>Copy the overlay link.</Text>
                                <Paragraph><Image style={{maxHeight:'150px'}} src="./step5.png" /></Paragraph>
                            </li>
                            <li>
                                <Text>Paste the link into 'Browser' source URL in OBS.</Text>
                                <Paragraph><Text type="warning" strong>Set the height to a minimum of 500. Anything less may cut your avatars.</Text></Paragraph>
                                <Paragraph><Image style={{maxHeight:'150px'}} src="./step6.png" /></Paragraph>
                            </li>
                            <li>
                                <AvatarsForm onSubmit={handleSubmit} />
                            </li>
                            <li>
                                <Text>Copy the generated CSS on the right and replace the 'Custom CSS' of 'Browser' source in OBS.</Text>
                                <Paragraph><Text type="warning" strong>Set the width and height to be high enough to see all the avatars added.</Text></Paragraph>
                                <Paragraph><Image style={{maxHeight:'150px'}} src="./step7.png" /></Paragraph>
                            </li>
                            <li>
                                <Text>Duplicate/Trim and customize the 'Browser' source in OBS to your layout.</Text>
                                <Paragraph><Text type="warning" strong>Each avatar is 400px wide and 400px tall with 50px spacing on all sides.</Text></Paragraph>
                                <Paragraph><Image style={{maxHeight:'150px'}} src="./step8.png" /></Paragraph>
                            </li>
                        </ol>
                    </Image.PreviewGroup>
                </Col>
                <Col span={1} />
                <Col span={11}>
                    <GeneratedCssContent generatedCss={generatedCss} />
                </Col>
            </Row>
        </Content>
    )
}

export default FirstTimeContent;