
export const generateAvatarTemplate = (userConfigs) =>
    userConfigs.map((userConfig, index) => {
        const avatarCss = !userConfig.avatarUrl ? '' : `img[data-reactid*="${userConfig.userId}"] {content: url('${userConfig.avatarUrl}');}\n`
        const speakingAvatarCss = !userConfig.speakingAvatarUrl ? '' : `img[data-reactid*="${userConfig.userId}"].speaking {content: url('${userConfig.speakingAvatarUrl}');}\n`
        const orderCss = `li[data-reactid*="${userConfig.userId}"] {order: ${index + 1};}\n`
        return `${avatarCss}${speakingAvatarCss}${orderCss}`
    }).join('\n');

export const generateTemplateCss = (avatarCss, idleBrightness, jumpHeight) =>
    `.avatar {
    border: none !important;
    border-radius: 0 !important;
    width: 400px !important;
    height: 400px !important;
    object-fit: contain;
    padding: 0;
    margin: 0 !important;
    padding-left: 50px;
    padding-top: 50px;
}

.avatar {
    filter: brightness(${idleBrightness}%);
}

.avatar.speaking {
    filter: unset !important;
    transform: translate(0, -${jumpHeight}%) !important;
}

.voice-states {
    display: flex !important;
    flex-wrap: wrap;
    height: 100%;
    width: 100%;
    margin: 0 !important;
    padding: 0 !important;
}

.voice-state {
    width: unset !important;
    height: unset !important;
    margin: 0 !important;
    float: unset !important;
}

.user {
    display: none !important;
}

${avatarCss}`;
