import React, { useState } from 'react';
import {
  Layout, Menu
} from 'antd';
import './App.less';
import FirstTimeContent from './components/Content/FirstTimeContent';
import GenerateContent from './components/Content/GenerateContent';
import {
  PlayCircleFilled,
  ToolFilled,
  SmileFilled
} from '@ant-design/icons';
import DemoContent from './components/Content/DemoContent';

const { Sider } = Layout;

const DEMO_PAGE = 'DEMO';
const FIRST_TIME_PAGE = 'FIRST';
const GENERATE_PAGE = 'GENERATE';

const App = () => {
  const [page, setPage] = useState(DEMO_PAGE);
  return (
    <Layout style={{width:'100%'}}>
      <Sider
        width='12rem'
        style={{
          overflow: 'auto',
          height: '100vh',
          position: 'fixed',
          left: 0,
        }}
      >
        <Menu theme='dark' defaultSelectedKeys={[DEMO_PAGE]} onClick={(value) => setPage(value.key)}>
          <Menu.Item key={DEMO_PAGE} icon={<PlayCircleFilled />}>
            Demo
          </Menu.Item>
          <Menu.Item key={FIRST_TIME_PAGE} icon={<SmileFilled />}>
            First Time Guide
          </Menu.Item>
          <Menu.Item key={GENERATE_PAGE} icon={<ToolFilled />}>
            Generate Overlay
          </Menu.Item>
        </Menu>
      </Sider>
      <Layout style={{ padding: '1rem', marginLeft: '12rem' }}>
        <DemoContent isVisible={page === DEMO_PAGE} />
        <FirstTimeContent isVisible={page === FIRST_TIME_PAGE} />
        <GenerateContent isVisible={page === GENERATE_PAGE} />
      </Layout>
    </Layout>
  )
};

export default App;